.main-nav {
  position: fixed;
  z-index: 1;
  left: -320px;
  transition: left 1s; }
  .main-nav__list {
    list-style: none;
    margin: 0;
    padding: 0; }
  .main-nav__toggle {
    position: fixed;
    display: block;
    background-color: #3d4e3f;
    width: 25px;
    height: 25px;
    border-radius: 15%;
    cursor: pointer;
    top: 6px;
    left: 2px;
    z-index: 2; }
    .main-nav__toggle::before {
      position: absolute;
      content: "";
      width: 15px;
      height: 2px;
      background-color: white;
      top: 7px;
      left: 5px; }
    .main-nav__toggle::after {
      position: absolute;
      content: "";
      width: 15px;
      height: 2px;
      background-color: white;
      top: 15px;
      left: 5px; }
  .main-nav--opened {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.9);
    width: 50%;
    height: 100vh;
    top: 0;
    left: 0; }

@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none; }
  .main-nav--closed .user-list__item {
    display: none; }
  .main-nav--nojs .main-nav__toggle {
    display: none; }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0; }
  .main-nav--nojs .site-list__item {
    display: block; } }

@media (min-width: 768px) {
  .main-nav {
    left: 0;
    width: 100%; }
    .main-nav__toggle {
      display: none; }
    .main-nav__list {
      display: none; } }

.site-list {
